"use client";

import React, { useEffect, useState, Suspense } from "react";
import { useSearchParams } from "next/navigation";
import { useApp } from "@/contexts/AppContext";
import { Budget } from "@/types/api";
import { BudgetList, BudgetForm } from "@/components/budgets";
import { Modal, Button } from "@/components/ui";
import { budgetApi } from "@/services/api";

function BudgetsPageContent() {
  const {
    budgets,
    categories,
    loading,
    error,
    loadBudgets,
    loadCategories,
    createBudget,
    updateBudget,
    deleteBudget,
    clearError,
  } = useApp();

  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [selectedBudget, setSelectedBudget] = useState<Budget | null>(null);
  const [budgetProgress, setBudgetProgress] = useState<Record<string, number>>({});
  const searchParams = useSearchParams();

  useEffect(() => {
    loadCategories();
    loadBudgets();
  }, [loadCategories, loadBudgets]);

  useEffect(() => {
    if (searchParams.get("openAdd") === "1" && !isCreateModalOpen) {
      setIsCreateModalOpen(true);
      // Очищаем query параметр после открытия
      window.history.replaceState({}, "", window.location.pathname);
    }
  }, [searchParams, isCreateModalOpen]);

  // Load actual budget progress from analytics API
  useEffect(() => {
    if (!budgets) return;

    const loadProgress = async () => {
      const progress: Record<string, number> = {};

      for (const budget of budgets) {
        try {
          const data = await budgetApi.getProgress(budget.id);
          progress[budget.id] = data.spent || 0;
        } catch {
          // В случае ошибки показываем 0
          progress[budget.id] = 0;
        }
      }

      setBudgetProgress(progress);
    };

    loadProgress();
  }, [budgets]);

  const handleCreate = async (data: any) => {
    try {
      await createBudget(data);
      setIsCreateModalOpen(false);
    } catch (err) {
      // Error is handled in context
    }
  };

  const handleEdit = (budget: Budget) => {
    setSelectedBudget(budget);
    setIsEditModalOpen(true);
  };

  const handleUpdate = async (data: any) => {
    if (!selectedBudget) return;
    try {
      await updateBudget(selectedBudget.id, data);
      setIsEditModalOpen(false);
      setSelectedBudget(null);
    } catch (err) {
      // Error is handled in context
    }
  };

  const handleDeleteClick = (id: string) => {
    const budget = budgets?.find((b) => b.id === id);
    if (budget) {
      setSelectedBudget(budget);
      setIsDeleteModalOpen(true);
    }
  };

  const handleDeleteConfirm = async () => {
    if (!selectedBudget) return;
    try {
      await deleteBudget(selectedBudget.id);
      setIsDeleteModalOpen(false);
      setSelectedBudget(null);
    } catch (err) {
      // Error is handled in context
    }
  };

  return (
    <div className="min-h-screen bg-background">
      <div className="mx-auto max-w-7xl px-4 py-8 sm:px-6 lg:px-8">
        <div className="mb-8 flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
          <h1 className="text-2xl font-semibold text-foreground md:text-3xl">
            Бюджеты
          </h1>
        </div>

        {error && (
          <div className="bg-destructive/10 border border-destructive/50 text-destructive px-4 py-3 rounded-lg mb-6 flex justify-between items-center">
            <span>{error}</span>
            <button
              onClick={clearError}
              className="text-destructive hover:text-destructive/80"
            >
              ✕
            </button>
          </div>
        )}

        <BudgetList
          budgets={budgets}
          categories={categories}
          budgetProgress={budgetProgress}
          loading={loading}
          onEdit={handleEdit}
          onDelete={handleDeleteClick}
        />

        <Modal
          isOpen={isCreateModalOpen}
          onClose={() => setIsCreateModalOpen(false)}
          title="Создать бюджет"
        >
          <BudgetForm
            categories={categories}
            onSubmit={handleCreate}
            onCancel={() => setIsCreateModalOpen(false)}
          />
        </Modal>

        <Modal
          isOpen={isEditModalOpen}
          onClose={() => {
            setIsEditModalOpen(false);
            setSelectedBudget(null);
          }}
          title="Редактировать бюджет"
        >
          {selectedBudget && (
            <BudgetForm
              budget={selectedBudget}
              categories={categories}
              onSubmit={handleUpdate}
              onCancel={() => {
                setIsEditModalOpen(false);
                setSelectedBudget(null);
              }}
            />
          )}
        </Modal>

        <Modal
          isOpen={isDeleteModalOpen}
          onClose={() => {
            setIsDeleteModalOpen(false);
            setSelectedBudget(null);
          }}
          title="Удалить бюджет"
          size="sm"
        >
          <div className="space-y-4">
            <p className="text-foreground">
              Вы уверены, что хотите удалить этот бюджет?
            </p>
            {selectedBudget && (
              <div className="bg-muted/30 p-3 rounded">
                <p className="font-medium text-foreground">
                  {selectedBudget.amount} USD
                </p>
                <p className="text-sm text-muted-foreground">
                  {selectedBudget.period === "monthly" ? "Месячный" : "Годовой"}
                </p>
              </div>
            )}
            <div className="flex gap-3">
              <Button variant="danger" onClick={handleDeleteConfirm} className="flex-1">
                Удалить
              </Button>
              <Button
                variant="secondary"
                onClick={() => {
                  setIsDeleteModalOpen(false);
                  setSelectedBudget(null);
                }}
                className="flex-1"
              >
                Отмена
              </Button>
            </div>
          </div>
        </Modal>
      </div>
    </div>
  );
}

export default function BudgetsPage() {
  return (
    <Suspense
      fallback={<div className="min-h-screen bg-background p-8">Загрузка...</div>}
    >
      <BudgetsPageContent />
    </Suspense>
  );
}
